import React, { useState } from "react";
import { Paper, Button, TextField } from "@material-ui/core";
import { CSS } from "../../assets/style";
import { call } from "../../services/Api";
import Error from "../RainyDay/Error";
import { CONTACT_US_FORM, validateFormAndMessage, getValue, CAREER_FORM } from "../../services/Form";
import { updateNotofication } from "../../Actions/ActionTypes";
import { convertToCamelCase } from "../../services/util";
import CustomBtn from "../CustomBtn";

const ContactUsForm = (props: any) => {
  const [formData, setFormData] = useState({});
  const [errorMessage, serErrorMessage] = useState('');
  const [isApiCallInProgress, setApiCallInProgress] = useState(false);
  const FORM = props.career ? CAREER_FORM : CONTACT_US_FORM;
  // /email/send?subject=Testing&html=<h1> TEST CONTENT</h1>
  const handleChange = (stateName: string, value: string) => {
    setFormData({ ...formData, [stateName]: value });
  }

  const send = () => {
    serErrorMessage('');
    if (validateFormAndMessage(formData, FORM).isInvalid) {
      serErrorMessage(validateFormAndMessage(formData, FORM).errorMessage)
      return
    }
    let formComponent = `<p>${getValue('name', formData)} need some ${props.career ? 'job' : ''} Informatiion.</p>
    <p>These are the Users Details</p>
    <table>${Object.keys(formData).map((key: string) => {
      return `<tr key=${key}><td><b>${convertToCamelCase(key)}</b></td><td>${getValue(key, formData)}</td></tr>`
    }).join('')}</table>`

    setApiCallInProgress(true);
    call(`email/send?subject=${getValue('subject', formData)}&html=${formComponent}`,
      'POST').then((response: any) => {
        setApiCallInProgress(false);
        if (response.responseCode || response.error) {
          serErrorMessage(response.responseCode || response.error)
          updateNotofication({ type: "error", message: response.responseCode || response.error });
        } else {
          setFormData({});
          updateNotofication({ type: "success", message: "Mesage Sent" });
        }
      })
  }

  const createTextField = (key: string, placeHolder: string, type: string, isTextArea?: boolean) => {
    return <TextField
      type={type}
      key={key}
      variant="outlined"
      size={"small"}
      fullWidth
      value={getValue(key, formData)}
      onChange={(e) => { handleChange(key, e.target.value) }}
      margin="normal"
      label={placeHolder}
      multiline={isTextArea}
      rows={3}
      rowsMax={4}
      inputProps={{
        maxLength: key === 'phone' ? 10 : undefined,
      }}
    />
  }

  const FormHeadeer = !props.career ? <h3 className="lead w-responsive text-center mx-auto mb-5">
    <b>Write us</b>
    <br />
    <small>
      We look forward to hearing from you
  </small>
  </h3> : null;

  return (
    <Paper elevation={3} className="p-3 mt-2">
      <form>
        {FormHeadeer}
        {FORM.map((formElement: any) => {
          return createTextField(formElement.key, formElement.label, formElement.type, formElement.textArea)
        })}
        <div className="text-center mt-4">
          <CustomBtn size="small" btnLabel={isApiCallInProgress ? "Sending..." : "Send"} />
        </div>
        <Error errorMessage={errorMessage} />
      </form>
    </Paper>
  );
};

export default ContactUsForm;
