import React, { Fragment, useEffect, useState } from 'react';
import { MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavItem } from 'mdbreact';
import { Link, withRouter } from 'react-router-dom';
import { COLORS, CSS } from '../assets/style';
import { Button, createStyles, Divider, Drawer, Hidden, IconButton, List, ListItem, makeStyles, Tab, Tabs, Theme } from '@material-ui/core';
import { MenuItems } from '../services/Constants';
import { connect } from 'react-redux';
import LoggedInUserIcon from './Login/LoggedInUserIcon';
import { LoginNew } from './Login/LoginNew';
import Cookies from 'js-cookie';
import MenuIcon from "@material-ui/icons/Menu";
import CustomBtn from './CustomBtn';
import Booking from '../pages/Booking';

const Image = require('../assets/icons/app_logo.png')

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1
    },
    menuButton: {
      marginRight: theme.spacing(2)
    },
    title: {
      flexGrow: 1
    },
    list: {
      width: 250,
      height: "100%"
    }
  })
);

function Header() {
  const [openDrawer, setOpen] = useState(false);
  const [isUserLoggedIn, setIsUserLoggedIn] = useState(false);

  useEffect(() => {
    checkCookie()
  }, []);

  const checkCookie = () => {
    if (Cookies.get('userDetails') !== undefined) {
      setIsUserLoggedIn(true);
    } else {
      setIsUserLoggedIn(false);
    }
  }
  const toggleDrawer = () => (event: React.KeyboardEvent | React.MouseEvent) => {
    if (
      event && event.type === "keydown" &&
      ((event as React.KeyboardEvent).key === "Tab" || (event as React.KeyboardEvent).key === "Shift")
    ) {
      return;
    }
    setOpen(!openDrawer);
  };

  const getSelectedMenuValue = () => {
    return window.location.pathname
  }

  const list = () => (
    <>
      <Link onClick={toggleDrawer()} className="d-flex justify-content-center m-2" to="/">
        <img alt="app_logo" height="50px" src={String(Image.default)} />
      </Link>
      <Divider className="mb-2 mt-2" />
      <div className={classes.list} role="presentation" onClick={toggleDrawer()} onKeyDown={toggleDrawer()}>
        <List>
          {MenuItems.map((item, index) => (
            <Fragment key={index}>
              <ListItem key={index}>
                <Link key={index} style={{ color: COLORS.app_orange }} to={item.value}>{item.label}</Link>
              </ListItem>
              <Divider className="mb-2 mt-2" />
            </Fragment>
          ))}
        </List>
      </div>
    </>
  );

  const classes = useStyles();

  return (
    <MDBNavbar style={{ ...CSS.navBar }} fixed="top" dark expand="md" scrolling className="pr-0">
      <Hidden mdUp>
        <IconButton edge="start" className={classes.menuButton} aria-label="menu" onClick={toggleDrawer()}>
          <MenuIcon />
        </IconButton>
      </Hidden>
      <Hidden smDown>
        <MDBNavbarBrand href="/">
          <img alt="app logo" height="50px" src={String(Image.default)} />
          <div className="float-right ml-1">
            <h4 style={{ color: COLORS.app_green }} className="mb-0">Company Name</h4>
            <small style={{ color: COLORS.app_orange }}>Company Tagline</small>
          </div>
        </MDBNavbarBrand>
      </Hidden>
      <MDBNavbarNav right>
        <Hidden smDown>
          <Tabs value={getSelectedMenuValue()}>
            {MenuItems.map((item: any, index: number) => {
              return <Tab key={index} label={item.label} value={item.value} component={Link} to={item.value} />
            })}
          </Tabs>
        </Hidden>
        <MDBNavItem className="d-flex align-items-center">
          <Booking />
          {isUserLoggedIn ? <LoggedInUserIcon successCB={checkCookie} /> : <LoginNew successCB={checkCookie} />}
        </MDBNavItem>
      </MDBNavbarNav>

      <Hidden mdUp>
        <Drawer anchor="left" open={openDrawer} onClose={toggleDrawer()}>
          {list()}
        </Drawer>
      </Hidden>
    </MDBNavbar>
  );
}

export default withRouter(connect(null)(Header))