import React from 'react'
import { CSS } from "../assets/style"

export default function Heading(props: any) {
    return <h1 className="text-center m-5 bgImg">
        <small style={{ ...CSS.smallHeader }}>{props.heading1} </small>
        <span style={{ ...CSS.header }}>{props.heading2}</span>
    </h1>
}