import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { MDBContainer, MDBRow, MDBCol } from 'mdbreact';
import { UserDetailsKeyMapping } from '../services/Constants';
import { call } from '../services/Api';
import Loading from '../components/RainyDay/Loading';
import Accordian from '../components/Accordian/Accordian';
// import CustomListItem from '../components/Cards/CustomListItem';
import { List, Paper, Divider, TextField, Button } from '@material-ui/core';
import ApiError from '../components/RainyDay/ApiError';
import { traverseValue, isJson } from '../services/util';
import InfoDialog from '../components/Dialog/InfoDialog';
import { MYACCOUNT_ADDRESS_FORM, getValue, validateFormAndMessage, MYACCOUNT_INFO } from '../services/Form';
import { CSS } from '../assets/style';
import Error from '../components/RainyDay/Error';
import { updateNotofication, updateUserDetails } from '../Actions/ActionTypes';
import LockIcon from '@material-ui/icons/Lock';
import ForgotPassword from '../components/Login/ForgotPassword';
import Cookies from 'js-cookie';

function MyAccount(props: any) {
    const [loggedInUserDetails, setLoggedInUserDetails] = useState({} as any);
    const [isApiError, setApiError] = useState(false);
    const [errorMessage, serErrorMessage] = useState('');

    const [formData, setFormData] = useState({});
    const [personalformData, setPersonalFormData] = useState({});

    const [addressDialogOpen, setAddressDialogOpen] = useState(false);
    const [infoDialogOpen, setInfoDialogOpen] = useState(false);
    const [passwordDialogOpen, setPasswordDialogOpen] = useState(false);


    const handleChange = (stateName: string, value: string) => {
        setFormData({ ...formData, [stateName]: value });
    }

    const handlePersonalInfoChange = (stateName: string, value: string) => {
        setPersonalFormData({ ...personalformData, [stateName]: value });
    }

    const unAuthorisedUser = <div className="m-5 d-flex justify-cintent-center align-items-center flex-column">
        {/* <ErrorImg className="m-5" width="100px" /> */}
            You are not authorised to view this page. Please Login to continue.
        </div>

    // eslint-disable-next-line react-hooks/exhaustive-deps
    const callUserDetailsAPI = () => {
        if (!props.userDetails._id) {
            return
        }
        setApiError(false);
        call(`user/${props.userDetails._id}`, 'GET').then((response: any) => {
            if (response.error) {
                setApiError(true);
            } else {
                setLoggedInUserDetails(response);
                // setPersonalFormData({ ...personalformData, ...response.user })
                // if (response.user.address && isJson(response.user.address)) {
                //     setFormData({ ...formData, ...JSON.parse(response.user.address) });
                // }
            }
        })
    }

    useEffect(() => {
        if (!isApiError)
            callUserDetailsAPI();
    }, [callUserDetailsAPI, isApiError, props.userDetails])

    if (props.userDetails._id === undefined) {
        return unAuthorisedUser
    }

    if (loggedInUserDetails.user === undefined) {
        return isApiError ? <ApiError /> : <Loading name="User Details" />
    }

    // const appointmentContent = loggedInUserDetails.appointments.map((appointment: any, index: number) => {

    //     return <List key={appointment._id}><CustomListItem {...{
    //         ...appointment,
    //         ...{ tests: loggedInUserDetails.tests ? loggedInUserDetails.tests.filter((test: any) => appointment.testIds.includes(test._id)) : [] },
    //         ...{ packages: loggedInUserDetails.packages ? loggedInUserDetails.packages.filter((test: any) => appointment.packageIds.includes(test._id)) : [] },
    //         ...{ cb: callUserDetailsAPI }
    //     }} /></List>
    // })

    const updateAddress = () => {
        serErrorMessage('');
        if (validateFormAndMessage(formData, MYACCOUNT_ADDRESS_FORM).isInvalid) {
            serErrorMessage(validateFormAndMessage(formData, MYACCOUNT_ADDRESS_FORM).errorMessage)
            return
        }
        let requestParam = {
            _id: props.userDetails._id,
            address: JSON.stringify(formData)
        }
        call('user', 'PUT', JSON.stringify(requestParam)).then((response: any) => {
            if (response.error || response.message) {
                serErrorMessage(response.error || response.message);
                updateNotofication({ type: "error", message: response.error || response.message });
            } else {
                updateNotofication({ type: "success", message: "Address Updated" });
                callUserDetailsAPI();
                setAddressDialogOpen(false);
                props.updateUserDetails(response);
                Cookies.set('userDetails', response, { expires: 30 });
            }
        })
    }

    const updateInfo = () => {
        serErrorMessage('');
        if (validateFormAndMessage(personalformData, MYACCOUNT_INFO).isInvalid) {
            serErrorMessage(validateFormAndMessage(personalformData, MYACCOUNT_INFO).errorMessage)
            return
        }
        let requestParam = {
            _id: props.userDetails._id,
            name: getValue('name', personalformData),
            emailId: getValue('emailId', personalformData)
        }

        call('user', 'PUT', JSON.stringify(requestParam)).then((response: any) => {
            if (response.error || response.message) {
                serErrorMessage(response.error || response.message);
                updateNotofication({ type: "error", message: response.error || response.message });
            } else {
                updateNotofication({ type: "success", message: "Info Updated" });
                callUserDetailsAPI();
                setInfoDialogOpen(false);
            }
        })
    }

    const createAddressComponent = () => {
        return <>
            {MYACCOUNT_ADDRESS_FORM.map((inputField: any) => {
                return <TextField
                    key={inputField.key}
                    className="w-100"
                    variant="outlined"
                    size={"small"}
                    value={getValue(inputField.key, formData)}
                    onChange={(e) => { handleChange(inputField.key, e.target.value) }}
                    margin="normal"
                    label={inputField.label}
                    type={inputField.type}
                />
            })}
            <div className="text-center mt-4">
                <Button size="small" variant="contained" style={{ ...CSS.btn, ...CSS.btnSuccess }} onClick={updateAddress} >
                    Save Address
                </Button>
            </div>
            <Error errorMessage={errorMessage} />
        </>
    }

    const createPersonalInfoComponent = () => {
        return <>
            {MYACCOUNT_INFO.map((inputField: any) => {
                return <TextField
                    key={inputField.key}
                    className="w-100"
                    variant="outlined"
                    size={"small"}
                    value={getValue(inputField.key, personalformData)}
                    onChange={(e) => { handlePersonalInfoChange(inputField.key, e.target.value) }}
                    margin="normal"
                    label={inputField.label}
                    type={inputField.type}
                />
            })}
            <div className="text-center mt-4">
                <Button size="small" style={{ ...CSS.btn, ...CSS.btnSuccess }} onClick={updateInfo} >
                    Update Info
                </Button>
            </div>
            <Error errorMessage={errorMessage} />
        </>
    }

    const createUpdatePasswordComponent = () => {
        return <div className="text-center">
            <ForgotPassword isUpdating={true} phoneNumber={loggedInUserDetails.user.phoneNumber} successCB={setPasswordDialogOpen} />
            <Error errorMessage={errorMessage} />
        </div>
    }

    return <MDBContainer className="mt-10 p-2">
        <MDBRow className="m-0">
            <MDBCol md="6">
                <h3 className="mb-3"> Your Account</h3>
                <Paper elevation={2} className="p-3 mb-3">
                    <table className="w-100">
                        <tbody>
                            {Object.keys(loggedInUserDetails.user).filter(key => UserDetailsKeyMapping[key] !== undefined).map((key: any, index: number) => {
                                return <tr key={index} >
                                    <th>{UserDetailsKeyMapping[key]}</th>
                                    <td>{traverseValue(key, loggedInUserDetails.user)}</td>
                                </tr>
                            })}
                            <tr>
                                <th>Password</th>
                                <td className="d-flex align-items-center">
                                    <span>#######</span>
                                    <InfoDialog
                                        isIconButton={true}
                                        iconButton={<LockIcon style={{ color: 'gray' }} />}
                                        open={passwordDialogOpen}
                                        setDialogOpen={setPasswordDialogOpen}
                                        title="Update Password"
                                        component={createUpdatePasswordComponent()} />
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div className="m-3 d-flex justify-content-center">
                        <InfoDialog
                            open={infoDialogOpen}
                            setDialogOpen={setInfoDialogOpen}
                            title="Account"
                            btnLabel="Edit Info"
                            component={createPersonalInfoComponent()} />
                    </div>
                </Paper>
            </MDBCol>
            <MDBCol md="6">
                <h3 className="mb-3">Your Address</h3>
                {loggedInUserDetails.user.address === undefined ?
                    <div className="m-3 d-flex justify-content-center">
                        <InfoDialog
                            open={addressDialogOpen}
                            setDialogOpen={setAddressDialogOpen}
                            title="Address"
                            btnLabel="Add Address"
                            component={createAddressComponent()} />
                    </div>
                    :
                    <Paper elevation={2} className="p-3 mb-3">
                        <table className="w-100">
                            <tbody>
                                {!isJson(loggedInUserDetails.user.address) ? null :
                                    Object.keys(JSON.parse(loggedInUserDetails.user.address)).filter(key => UserDetailsKeyMapping[key] !== undefined).map((key: any, index: number) => {
                                        return <tr key={index} >
                                            <th>{UserDetailsKeyMapping[key]}</th>
                                            <td>{traverseValue(key, JSON.parse(loggedInUserDetails.user.address))}</td>
                                        </tr>
                                    })
                                }
                            </tbody>
                        </table>
                        <div className="m-3 d-flex justify-content-center">
                            <InfoDialog
                                open={addressDialogOpen}
                                setDialogOpen={setAddressDialogOpen}
                                title="Address"
                                btnLabel="Edit Address"
                                component={createAddressComponent()} />
                        </div>
                    </Paper>
                }
            </MDBCol>
        </MDBRow>
        <Divider className="mt-2 mb-2" />
        <h3 className="mb-3"> Your Appointments</h3>
        {/* <Accordian content={appointmentContent} acoordianHeaderLabel="Appointment" open={true} /> */}
    </MDBContainer>
}

const mapStateToProps = (state: any) => {
    return {
        userDetails: state.UserReducer.userDetails
    }
}

export default connect(mapStateToProps, { updateUserDetails })(MyAccount);