import React from 'react';
import {
    MDBContainer,
    MDBCarousel,
    MDBCarouselInner,
    MDBCarouselItem,
    MDBIcon
} from 'mdbreact';
import { Avatar } from '@material-ui/core';
import { CSS } from '../../assets/style';

const TestimonialsPage = () => {
    const testimonialList = [{
        id: 1,
        title: "User Name",
        designation: "Software Developer",
        avatar: "https://mdbootstrap.com/img/Photos/Avatars/img%20(30).jpg",
        message: "Nemo enim ipsam voluptatem quia voluptas sit aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos qui ratione voluptatem sequi nesciunt. Neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit, sed quia non numquam eius modi tempora incidunt ut labore.",
        rating: 4,
    }, {
        id: 2,
        title: "User Name2",
        designation: "Software Developer",
        avatar: "https://mdbootstrap.com/img/Photos/Avatars/img%20(30).jpg",
        message: "Nemo enim ipsam voluptatem quia voluptas sit aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos qui ratione voluptatem sequi nesciunt. Neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit, sed quia non numquam eius modi tempora incidunt ut labore.",
        rating: 2,
    }]

    function getRatings(rating: number) {
        let starRating = [];
        for (let step = 0; step < rating; step++) {
            starRating.push(<MDBIcon key={step} icon='star' className='blue-text' />)
        }
        if (rating < 5) {
            for (let step = 0; step < 5 - rating; step++) {
                starRating.push(<MDBIcon key={step + 'far'} far icon='star' className='blue-text' />)
            }
        }
        return starRating
    }
    return (
        <MDBContainer>
            <section className='text-center my-5'>
                <MDBCarousel
                    activeItem={1}
                    length={testimonialList.length}
                    testimonial
                    showIndicators={false}
                    className='no-flex'
                >
                    <MDBCarouselInner>
                        {testimonialList.map((listItem, index) => <MDBCarouselItem key={listItem.id} itemId={++index}>
                            <div>
                                <Avatar className='mx-auto mb-4'>
                                    <img
                                        src={listItem.avatar}
                                        className='rounded-circle img-fluid'
                                        alt=''
                                    />
                                </Avatar>
                                <p>
                                    <MDBIcon icon='quote-left' />{listItem.message}
                                </p>
                                <h4 style={{ ...CSS.header }}>{listItem.title}</h4>
                                <h6 className='my-3' style={{ ...CSS.smallHeader }}>{listItem.designation}</h6>
                                {getRatings(listItem.rating)}
                            </div>
                        </MDBCarouselItem>)}
                    </MDBCarouselInner>
                </MDBCarousel>
            </section>
        </MDBContainer>
    );
};

export default TestimonialsPage;