import { MDBMask, MDBView, MDBContainer, MDBRow, MDBCol } from "mdbreact";
import React, { useEffect, useState } from 'react';
import Heading from '../components/Heading';
import NotFound from "../components/RainyDay/NotFound";
import { fetchAPI } from "../services/Api";
import ImageGallery from 'react-image-gallery';
import "../../node_modules/react-image-gallery/styles/css/image-gallery.css";
import { Dialog, DialogContent, DialogTitle, IconButton, makeStyles, Theme, createStyles, Hidden, Button } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import CustomBtn from "../components/CustomBtn";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        closeButton: {
            position: 'absolute',
            right: theme.spacing(5),
            top: theme.spacing(5),
            zIndex: 999
        },
    })
);

export default function Gallery() {
    const classes = useStyles();
    const [imageList, setImageList] = useState([]);
    const [videoList, setVideoList] = useState([]);
    const [open, setOpen] = useState(false);
    const [slideIndex, setSlideIndex] = useState(0)

    useEffect(() => {
        getImageList();
        getVideoList();
    }, []);

    const getImageList = () => {
        fetchAPI("images").then((res: any) => {
            res = res && res.map((element: any) => { return { ...element, ...{ original: element.src, thumbnail: element.src } } })
            setImageList(res || [])
        })
    }

    const getVideoList = () => {
        fetchAPI("videos").then((res: any) => {
            res = res && res.map((element: any) => { return { ...element, ...{ original: element.src, thumbnail: element.src } } })
            setVideoList(res || [])
        })
    }


    return <MDBContainer>
        <Heading heading1="our" heading2="Gallery" />
        {imageList.length === 0 ? <NotFound listType="Media" /> : <MDBRow>
            {imageList.map((image: any, index: number) => <MDBCol md="4" onClick={() => { setOpen(true); setSlideIndex(index) }}>
                <MDBView hover className="btn p-0">
                    <img
                        src={image.src}
                        className="img-fluid"
                        alt=""
                    />
                    <MDBMask className="flex-center" overlay="black-strong">
                        <p className="white-text">{image.title}</p>
                    </MDBMask>
                </MDBView>
            </MDBCol>)}
        </MDBRow>}
        <Dialog
            onBackdropClick={() => { setOpen(false) }}
            PaperProps={{
                style: {
                    backgroundColor: 'black',
                    boxShadow: 'none',
                },
            }}
            fullScreen
            open={open}
            onEscapeKeyDown={() => { setOpen(false) }}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <Hidden mdDown>
                <DialogTitle id="alert-dialog-title">
                    <IconButton aria-label="close" className={classes.closeButton} onClick={() => { setOpen(false) }}>
                        <CloseIcon fontSize="large" color="secondary" />
                    </IconButton>
                </DialogTitle>
            </Hidden>
            <DialogContent className="text-center" style={{ height: 'calc(80vh)' }}>
                <ImageGallery startIndex={slideIndex} items={imageList} />
                <Hidden lgUp>
                    <CustomBtn btnLabel="Done" handleButtonClick={() => { setOpen(false) }} />
                </Hidden>
            </DialogContent>
        </Dialog>
    </MDBContainer>
}