import * as Actions from '../Actions/ActionTypes';

const initialState = {
    auth_userName: '',
    auth_password: '',
    userDetails: {
        _id: 'Anup',
        userType: undefined,
        userId: undefined
    },
    cartItems: [],
}

export const UserReducer = (state = initialState, action: any) => {
    switch (action.type) {
        case Actions.USER_DETAILS:
            return Object.assign({}, state, {
                userDetails: action.userDetails
            })
        case Actions.AUTH_USERNAME:
            return Object.assign({}, state, {
                auth_userName: action.auth_userName
            })
        case Actions.AUTH_PASSWORD:
            return Object.assign({}, state, {
                auth_password: action.auth_password
            })
        default:
            return state;
    }
}
