import React from 'react';
import { IconButton, Menu, MenuItem } from "@material-ui/core";
import Cookies from 'js-cookie';
import { updateUserDetails, updateNotofication } from '../../Actions/ActionTypes';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { CSS } from '../../assets/style';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

function LoggedInUserIcon(props: any) {
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
    const open = Boolean(anchorEl);

    const handleMenu = (event: React.MouseEvent<HTMLElement>) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleLogOutUser = () => {
        Cookies.remove('auth_userName');
        Cookies.remove('auth_password');
        Cookies.remove('userDetails');
        props.updateUserDetails({})
        props.successCB();
        setAnchorEl(null);
        updateNotofication({ type: "info", message: "User logout" });
    }

    return <div>
        <IconButton
            aria-label="account of current user"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleMenu}
            color="inherit"
        >
            <ExitToAppIcon  style={CSS.btn}/>
        </IconButton>
        <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
            }}
            keepMounted
            transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
            }}
            open={open}
            onClose={handleClose}
        >
            <MenuItem onClick={handleLogOutUser} component={Link} to={'/'}>Logout</MenuItem>
        </Menu>
    </div>
}

const mapStateToProps = (state: any) => {
    return {
        userType: state.UserReducer.userDetails.userType,
        cartItems: state.UserReducer.cartItems
    }
}

export default connect(mapStateToProps, { updateUserDetails })(LoggedInUserIcon);