import React from "react";
import ActionButton from "../ActionButton";

export default function FigureCard(props: any) {
    return (
        <figure className="text-center shadow">
            {props.figure ? <img src={props.src} className="figure-img img-fluid z-depth-1 w-100 m-0 mb-3"
                alt="" style={{ width: '200px', margin: '10px' }} /> :
                <iframe style={{ "minHeight": "300px" }} className="w-100 border-0" title="video" src={props.src} allowFullScreen></iframe>}
            <figcaption className="figure-caption text-center">
                <b>{props.title}</b>
                <p>{props.description}</p>
                <p className="text-danger">{props.price}</p>
                <ActionButton {...props} />
            </figcaption>
        </figure>
    );
}