import React, { useState } from 'react';
import { TextField, Button } from '@material-ui/core';
import { call } from '../../services/Api';
import { CSS } from '../../assets/style';
import { REGISTRATION_FORM, getValue, validateFormAndMessage, emptyErrorMsg } from '../../services/Form';
import Error from '../RainyDay/Error';
import { updateNotofication } from '../../Actions/ActionTypes';
import CustomBtn from '../CustomBtn';

export default function Register(props: any) {
    const [formData, setFormData] = useState({});
    const [errorMessage, serErrorMessage] = useState('');
    const [isOTPSectionVisible, showotpSection] = useState(false);

    const handleChange = (stateName: string, value: string) => {
        setFormData({ ...formData, [stateName]: value });
    }

    const createTextField = (key: string, placeHolder: string, type?: string) => {
        return <TextField
            key={key}
            className="w-100"
            variant="outlined"
            size={"small"}
            value={getValue(key, formData)}
            onChange={(e) => { handleChange(key, e.target.value) }}
            margin="normal"
            label={placeHolder}
            type={type}
        />
    }

    const registerUser = () => {
        serErrorMessage('');
        if (validateFormAndMessage(formData, REGISTRATION_FORM).isInvalid) {
            serErrorMessage(validateFormAndMessage(formData, REGISTRATION_FORM).errorMessage)
            return
        }
        let requestParam = {
            name: getValue('name', formData),
            emailId: getValue('email', formData),
            phoneNumber: getValue('phone', formData),
            password: getValue('password', formData)
        }
        call('auth/create', 'POST', JSON.stringify(requestParam)).then((response: any) => {
            if (response.error) {
                serErrorMessage(response.error)
                updateNotofication({ type: "error", message: response.error });
            } else {
                showotpSection(true);
            }
        })
    }

    const activateUser = () => {
        serErrorMessage('');
        if (getValue('otp', formData).length === 0) {
            serErrorMessage(emptyErrorMsg('OTP'))
            return
        }
        let requestParam = {
            phoneNumber: getValue('phone', formData),
            otp: getValue('otp', formData)
        }
        call('auth/activate', 'POST', JSON.stringify(requestParam)).then((response: any) => {
            if (response.error) {
                serErrorMessage(response.error);
                updateNotofication({ type: "error", message: response.error });
            } else {
                updateNotofication({ type: "success", message: "Registration Successful" });
                showotpSection(false);
                props.successCB(false);
            }
        })
    }

    const otpSection = () => {
        if (!isOTPSectionVisible) {
            return null
        }
        return <>
            {createTextField('otp', "Enter OTP", "number")}
            <br />
            <Button onClick={registerUser} className="resend-otp" size="small" variant="text">Resend OTP</Button>
        </>
    }

    return <>
        {REGISTRATION_FORM.map(formElement => {
            return createTextField(formElement.key, formElement.label, formElement.type)
        })}
        {otpSection()}
        <br />
        <CustomBtn btnLabel={isOTPSectionVisible ? 'Validate OTP' : 'Register'} handleButtonClick={isOTPSectionVisible ? activateUser : registerUser} />
        <Error errorMessage={errorMessage} />
    </>
}