import store from "../Reducers";
import Cookies from 'js-cookie';

export const API_STATUS = 'API_STATUS';
export const AUTH_USERNAME = 'AUTH_USERNAME';
export const AUTH_PASSWORD = 'AUTH_PASSWORD';
export const USER_DETAILS = 'USER_DETAILS';
export const USER_CART = 'USER_CART';
export const SLOT_DETAILS = 'SLOT_DETAILS';

export function updateNotofication(notification: any) {
    store.dispatch({
        type: API_STATUS,
        apiStatus: {
            type: notification.type,
            message: notification.message
        }
    })
}

export function updateCart(cartItems: any) {
    return {
        type: USER_CART,
        cartItems
    }
}

export function updateSlots(slotDetails: any) {
    store.dispatch({
        type: SLOT_DETAILS,
        slotDetails
    })
}

export function updateUserDetails(userDetails: any) {
    return {
        type: USER_DETAILS,
        userDetails
    }
}

export function updateAuthUserNamePassword(userName: string, password: string) {
    store.dispatch({
        type: AUTH_USERNAME,
        auth_userName: userName
    })
    store.dispatch({
        type: AUTH_PASSWORD,
        auth_password: password
    })
}

export function updateStoreFromCookie(cookie: string, type: string, value: string) {
    if (Cookies.get(cookie) !== undefined) {
        store.dispatch({
            type,
            [value]: Cookies.get(cookie)
        })
    }
}