import { TextField } from '@material-ui/core'
import React, { useState } from 'react'
import { Link } from 'react-router-dom';
import { updateNotofication } from '../../Actions/ActionTypes';
import { fetchAPI } from '../../services/Api';
import { BOOKING_ADDRESS_FORM, getValue, validateFormAndMessage } from '../../services/Form'
import CustomBtn from '../CustomBtn';
import Error from '../RainyDay/Error';

export default function BookingDetailsForm(props: any) {
    const [formData, setFormData] = useState({});
    const [errorMessage, serErrorMessage] = useState('');

    const bookTest = () => {
        serErrorMessage('');
        if (validateFormAndMessage(formData, BOOKING_ADDRESS_FORM).isInvalid) {
            serErrorMessage(validateFormAndMessage(formData, BOOKING_ADDRESS_FORM).errorMessage)
            return
        }
        let addressObj = {
            house: getValue('house', formData),
            city: getValue('city', formData),
            state: getValue('state', formData),
            pin: getValue('pin', formData),
        }

        // let requestParam = {
        //     testIds: props.cartItems.filter((element: any) => element.category === CATEGORY_TEST).map((test: any) => test._id),
        //     packageIds: props.cartItems.filter((element: any) => element.category === CATEGORY_PACKAGE).map((test: any) => test._id),
        //     labId: isHomePicked ? selectLab : '',
        //     userId: props.userDetails._id,
        //     time: `${selectedDate.toJSON().split('T')[0]} ${selectedTime.toJSON().split('T')[1].split(".")[0]}`,
        //     homeCollection: isHomePicked,
        //     userDetails: {
        //         name: getValue('name', formData),
        //         emailId: getValue('email', formData),
        //         phoneNo: getValue('phone', formData),
        //         address: JSON.stringify(addressObj)
        //     }
        // }
        fetchAPI('appointment', 'POST', JSON.stringify({})).then((response: any) => {
            if (response.errors || response.error) {
                serErrorMessage(response._message || response.error);
                updateNotofication({ type: "error", message: response._message || response.error });
            } else {
                updateNotofication({ type: "success", message: "Appointmet Scheduled" });
            }
        })
    }

    const showAddAddressSection = () => {
        return props.userDetails.address ? null :
            <div className="mt-3 mb-3">
                <small>Add Address in <Link onClick={props.hanldeClose} to="/myaccount">My Account</Link> for Faster Booking.</small>
            </div>
    }


    const handleChange = (stateName: string, value: string) => {
        setFormData({ ...formData, [stateName]: value });
    }
    return <>
        {showAddAddressSection()}
        {BOOKING_ADDRESS_FORM.map((inputField: any) => {
            return <TextField
                key={inputField.key}
                className="w-100"
                variant="outlined"
                size={"small"}
                value={getValue(inputField.key, formData)}
                onChange={(e) => { handleChange(inputField.key, e.target.value) }}
                margin="normal"
                label={inputField.label}
                type={inputField.type}
            />
        })}
        <div className="d-flex justify-content-center w-100 mt-3">
            <CustomBtn handleButtonClick={bookTest} btnLabel="Book Now" />
        </div>
        <Error errorMessage={errorMessage} />
    </>
}