import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import { TransitionProps } from '@material-ui/core/transitions';
import CustomBtn from '../components/CustomBtn';
import { MDBContainer } from 'mdbreact';
import { CSS } from '../assets/style';
import BookingStepper from '../components/Bookings/Stepper';

const useStyles = makeStyles((theme) => ({
    appBar: {
        position: 'relative',
        background: '#fff'
    },
    title: {
        flex: 1,
    },
}));

const Transition = React.forwardRef<unknown, TransitionProps>(function Transition(props: any, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


export default function Booking() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <CustomBtn btnLabel="Book Appointment" handleButtonClick={handleClickOpen} />
            <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
                <AppBar className={classes.appBar}>
                    <Toolbar>
                        <IconButton edge="start" onClick={handleClose} aria-label="close">
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>Book Appointment</Typography>
                        <CustomBtn btnLabel="Confirm" handleButtonClick={handleClose} />
                    </Toolbar>
                </AppBar>

                <MDBContainer className="h-100 mt-2 p-0 pl-1">
                    <BookingStepper hanldeClose={handleClose} />
                </MDBContainer>

            </Dialog>
        </div>
    );
}
