import * as Actions from '../Actions/ActionTypes';
import moment from 'moment';

const currentDateInMMDDYYYY = moment().format('M/DD/YYYY')

const initialState = {
    slotDetails: {
        date: currentDateInMMDDYYYY,
        time: null,
        details: null,
    }
}

export const SlotReducer = (state = initialState, action: any) => {
    switch (action.type) {
        case Actions.SLOT_DETAILS:
            return Object.assign({}, state, {
                slotDetails: action.slotDetails
            })
        default:
            return state;
    }
}
