import React from 'react';
import { MDBCard, MDBCardImage, MDBCardTitle } from 'mdbreact';
import { CSS } from '../../assets/style';
import { Link } from 'react-router-dom';

const ViewMoreCard = (props: any) => {

    return (
        <Link to={props.link}><MDBCard style={{ ...CSS.alignCenter }} className="h-100 border-0">
            <MDBCardImage
                overlay='white-slight'
                className='card-img-top w-50 m-auto'
                src="https://icon-library.com/images/view-more-icon/view-more-icon-0.jpg"
                alt='View More'
            />

            <MDBCardTitle className='mt-3 text-center'>
                View More
            </MDBCardTitle>
        </MDBCard>
        </Link>
    )
}

export default ViewMoreCard;