import React, { useState, useEffect } from 'react';
import { FORGOT_PASSWORD_FORM, getValue, invalidErrorMsg, emptyErrorMsg } from '../../services/Form';
import { TextField, Button } from '@material-ui/core';
import { CSS } from '../../assets/style';
import Error from '../RainyDay/Error';
import { call } from '../../services/Api';
import { PHONE_REGEX } from '../../services/Constants';
import { updateNotofication, updateAuthUserNamePassword } from '../../Actions/ActionTypes';
import Cookies from 'js-cookie';
import CustomBtn from '../CustomBtn';

export default function ForgotPassword(props: any) {
    const [formData, setFormData] = useState({} as any);
    const [errorMessage, serErrorMessage] = useState('');
    const [isOTPSectionVisible, showotpSection] = useState(false);

    const handleChange = (stateName: string, value: string) => {
        setFormData({ ...formData, [stateName]: value });
    }

    useEffect(() => {
        if (props.isUpdating) {
            setFormData({ ...formData, 'phone': props.phoneNumber })
        }
    }, [formData, props.isUpdating, props.phoneNumber]);

    const createTextField = (key: string, placeHolder: string, type?: string) => {
        return <TextField
            key={key}
            className="w-100"
            variant="outlined"
            size={"small"}
            value={getValue(key, formData)}
            onChange={(e) => { handleChange(key, e.target.value) }}
            margin="normal"
            label={placeHolder}
            type={type}
            disabled={key === 'phone' && props.isUpdating}
        />
    }

    const getOtp = () => {
        serErrorMessage('');
        if (!PHONE_REGEX.test(getValue('phone', formData))) {
            serErrorMessage(invalidErrorMsg('Phone Number'))
            return
        }
        let requestParam = {
            phoneNumber: getValue('phone', formData),
            password: getValue('password', formData)
        }
        call('auth/forgot-password', 'POST', JSON.stringify(requestParam)).then((response: any) => {
            if (response.error) {
                serErrorMessage(response.error);
                updateNotofication({ type: "error", message: response.error });
            } else {
                showotpSection(true);
            }
        })
    }

    const activateUser = () => {
        serErrorMessage('');
        if (getValue('otp', formData).length === 0) {
            serErrorMessage(emptyErrorMsg('OTP'))
            return
        }
        let requestParam = {
            phoneNumber: getValue('phone', formData),
            otp: getValue('otp', formData)
        }
        call('auth/activate', 'POST', JSON.stringify(requestParam)).then((response: any) => {
            if (response.error) {
                serErrorMessage(response.error)
                updateNotofication({ type: "error", message: response.error });
            } else {
                updateNotofication({ type: "success", message: "Password successfully changed" });
                props.successCB(false);

                // only called when updating password from my account
                if (props.isUpdating) {
                    updateAuthUserNamePassword(formData.phone, formData.password);
                    Cookies.set('auth_password', formData.password, { expires: 30 });
                }
            }
        })
    }

    const otpSection = () => {
        if (!isOTPSectionVisible) {
            return null
        }
        return <>
            {createTextField('otp', "Enter OTP", "number")}
            <br />
            <Button onClick={getOtp} className="resend-otp" size="small" variant="text">Resend OTP</Button>
        </>
    }

    return <>
        {FORGOT_PASSWORD_FORM.map(formElement => {
            return createTextField(formElement.key, formElement.label, formElement.type)
        })}
        {otpSection()}
        <br />
        <CustomBtn btnLabel={isOTPSectionVisible ? 'Update Password' : 'Get OTP'} handleButtonClick={isOTPSectionVisible ? activateUser : getOtp} />
        <Error errorMessage={errorMessage} />
    </>
}