import React, { useEffect } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { IconButton, makeStyles, Theme, createStyles } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { CSS } from '../../assets/style';
import CustomBtn from '../CustomBtn';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        closeButton: {
            position: 'absolute',
            right: theme.spacing(1),
            top: theme.spacing(1)
        },
    })
);

export default function InfoDialog(props: any) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    useEffect(() => {
        if (props.open !== undefined) {
            setOpen(props.open)
        }
    }, [props.open])

    const handleClickOpen = () => {
        if (props.setDialogOpen) {
            props.setDialogOpen(true)
        };
        setOpen(true);
    };

    const handleClose = () => {
        if (props.resetAll) {
            props.resetAll()
        }
        setOpen(false);
    };

    return (
        <>
            {props.isIconButton ?
                <IconButton
                    disabled={props.isDisabled}
                    style={{ opacity: `${props.isDisabled ? .2 : 1}` }}
                    onClick={handleClickOpen}
                    title={props.title}>
                    {props.iconButton}
                </IconButton>
                : <CustomBtn btnLabel={props.btnLabel} handleButtonClick={handleClickOpen} />}

            <Dialog
                fullWidth
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    <span style={props.title.length > 40 ? CSS.smallHeader : CSS.header} >{props.title}</span>
                    <IconButton aria-label="close" className={classes.closeButton} onClick={handleClose}>
                        <CloseIcon />
                    </IconButton>
                </DialogTitle>
                <DialogContent>
                    {props.component}
                </DialogContent>
            </Dialog>
        </>
    );
}
