export function convertToDateString(string: Date) {
    return new Date(string).toDateString()
}

export function traverseValue(key: string, item: any) {
    if (key === 'createdDate' || key === 'time') {
        return convertToDateString(item[key])
    }
    if (key === 'homeCollection') {
        return item[key] ? 'YES' : 'NO'
    }
    if (key === 'address') {
        if (!isJson(item[key])) {
            return item[key]
        } else {
            let addressObj = JSON.parse(item[key]);
            let fullAddress = Object.keys(addressObj).map(key => `${addressObj[key]}`).join(', ');
            return fullAddress
        }

    }
    return item[key]
}

export function isJson(str: string) {
    try {
        JSON.parse(str);
    } catch (e) {
        return false;
    }
    return true;
}

export function getEplipsisText(value: string, limit?: number) {
    if (!value) {
        return ""
    } else {
        return value.substr(0, (limit || 20)) + (value.length > (limit || 20) ? "..." : "")
    }
}

export function convertToCamelCase(string: string) {
    return (string.substring(0, 1).toUpperCase() + string.substr(1, string.length))
}