import React, { useEffect, useState } from "react";
import { MDBCol, MDBContainer, MDBRow, MDBFooter, MDBIcon } from "mdbreact";
import { COLORS, CSS } from "../../assets/style";
import { fetchAPI } from "../../services/Api";
import { MenuItems } from "../../services/Constants";
import { Link } from "react-router-dom";
import { get } from "lodash";

const Footer = () => {
    const [slots, setSlots] = useState([]);
    const [contactDetails, setContactDetails] = useState({} as any);

    useEffect(() => {
        getContactDetails();
        getOperatingHours();
    }, []);

    const getContactDetails = () => {
        fetchAPI("contact").then((res: any) => {
            setContactDetails(res || [])
        })
    }

    const getOperatingHours = () => {
        fetchAPI("operatingSlots").then((res: any) => {
            setSlots(res || [])
        })
    }

    return (
        <MDBFooter style={{ background: "#656565" }} className="font-small pt-5 mt-5">
            <MDBContainer className="text-center text-md-left">
                <MDBRow className="text-center">
                    <MDBCol md="3">
                        <div className="ml-1">
                            <h4 className="mb-0 text-light">Company Name</h4>
                            <small style={{ color: COLORS.app_orange }}>Company Tagline</small>
                        </div>
                        <hr className="hr-light my-4" />
                    </MDBCol>
                    <MDBCol md="3">
                        <h5 className="title" style={{ ...CSS.smallHeader }}>Links</h5>
                        {MenuItems.map((item, index) => {
                            return <p key={index}><Link to={item.value}>{item.label}</Link></p>
                        })}
                    </MDBCol>
                    <MDBCol md="3">
                        <h5 className="title" style={{ ...CSS.smallHeader }}>Contact Us</h5>
                        <ul className="list-unstyled ml-4">
                            <li><MDBIcon icon="map-marker-alt" className="pr-2 text-light" />{get(contactDetails.address, 'house', null) + " , "
                                + get(contactDetails.address, 'street', null) + " , "
                                + get(contactDetails.address, 'city', null) + " , "
                                + get(contactDetails.address, 'state', null)}</li>
                            <li><MDBIcon icon="phone" className="pr-2 text-light" />{get(contactDetails, 'phone[0]', null)}</li>
                            <li><MDBIcon icon="envelope" className="pr-2 text-light" />{get(contactDetails, 'email[0]', null)}
                            </li>
                        </ul>
                        <hr className="hr-light my-4" />
                        <ul className="list-inline text-center list-unstyled">
                            <li className="list-inline-item">
                                <a rel="noreferrer" target="_blank" href={get(contactDetails.social, 'twitter', null)} className="p-2 fa-lg w-ic">
                                    <MDBIcon fab icon="twitter" />
                                </a>
                            </li>
                            <li className="list-inline-item">
                                <a rel="noreferrer" target="_blank" href={get(contactDetails.social, 'fb', null)} className="p-2 fa-lg w-ic">
                                    <MDBIcon fab icon="facebook" />
                                </a>
                            </li>
                            <li className="list-inline-item">
                                <a rel="noreferrer" target="_blank" href={get(contactDetails.social, 'insta', null)} className="p-2 fa-lg w-ic">
                                    <MDBIcon fab icon="instagram" />
                                </a>
                            </li>
                        </ul>
                        <hr className="hr-light my-4" />
                    </MDBCol>
                    <MDBCol md="3">
                        <h5 className="title" style={{ ...CSS.smallHeader }}>Opening Hours</h5>
                        <ul className="list-unstyled text-left">
                            {slots.map((day: any) => <li key={day.id} className="p-1 border-bottom">{day.label}
                                <span className={`${day.isClosed ? 'text-danger' : ''} float-right`} > {day.isClosed ? 'Closed' : `${day.startTime} - ${day.endTime}`}</span>
                            </li>)}
                        </ul>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
            <div className="footer-copyright text-center py-3">
                <MDBContainer fluid>
                    &copy; {new Date().getFullYear()} Copyright: <a href="https://www.theworkboxsolutions.com"> theworkboxsolutions.com </a>
                </MDBContainer>
            </div>
        </MDBFooter>
    );
}

export default Footer;