import React from "react";
import { CSS } from "../assets/style";
import { MDBContainer, MDBRow, MDBCol } from "mdbreact";
import ContactUsForm from "../components/Form/ContactUsForm";
import ContactUsList from "../components/Contact/ContactUsList";

export default function ContactUs() {
  return (
    <div>
      <div style={CSS.alignCenter} className="parallax parallax_contact text-center">
        <h2 className="text-light w-75 p-5 m-auto">Now We are ready to help you
        <br />
          <small>Our phone support is open everyday</small>
        </h2>
      </div>
      <MDBContainer className="p-0">
        <MDBRow className="m-3">
          <MDBCol md="6">
            <ContactUsList />
          </MDBCol>
          <MDBCol md="6">
            <ContactUsForm />
          </MDBCol>
        </MDBRow>
      </MDBContainer>
      <iframe
        className="mt-10"
        title="map"
        width="100%"
        height="500"
        id="gmap_canvas"
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3782.3138333050524!2d73.77938061530857!3d18.559884772813724!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc2becfb828e839%3A0x5375ed2872864fd3!2s1%2C%20Pan%20Card%20Club%20Rd%2C%20Baner%2C%20Pune%2C%20Maharashtra%20411045!5e0!3m2!1sen!2sin!4v1616481108494!5m2!1sen!2sin"
        frameBorder="0"
        scrolling="no">
      </iframe>

    </div>
  );
}
