import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import { Divider, IconButton, Theme, createStyles, makeStyles, Hidden } from "@material-ui/core";
import { CSS, COLORS } from "../../assets/style";
import { MDBRow } from "mdbreact";
import Login from "./Login";
import Register from "./Register";
import ForgotPassword from "./ForgotPassword";
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: COLORS.app_orange
    },
  })
);

export function LoginNew(props: any) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [newUser, setNewUser] = useState(false);
  const [forgotPassword, setForgotPassword] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const resetAll = () => {
    setNewUser(false);
    setForgotPassword(false);
  }

  const createHeaderText = () => {
    let headerText = "Login";
    if (newUser) {
      headerText = "Register"
    }
    if (forgotPassword) {
      headerText = "Forgot Password"
    }
    return <span style={CSS.header}>{headerText}</span>
  }

  const getForm = () => {
    let form = <Login successCB={props.successCB} modalCb={setOpen} />;
    if (newUser) {
      form = <Register successCB={setNewUser} />
    }
    if (forgotPassword) {
      form = <ForgotPassword successCB={setForgotPassword} />
    }
    return form;
  }

  return (
    <div>
      <IconButton
        aria-label="account of current user"
        aria-controls="menu-appbar"
        aria-haspopup="true"
        onClick={handleClickOpen}
        color="default"
      >
        <AccountCircleIcon />
      </IconButton>
      <Dialog fullWidth open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle style={CSS.header} id="form-dialog-title">
          {createHeaderText()}
          <IconButton aria-label="close" className={classes.closeButton} onClick={handleClose}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        {/* <Hidden smDown>
          <img style={{ alignSelf: "center" }} width="100" alt="login_icon" src={require('../../assets/icons/unisex.png').default} />
        </Hidden> */}
        <DialogContent className="text-center">
          {getForm()}
        </DialogContent>
        <Divider className="m-3" />
        <MDBRow className="m-0 d-flex flex-row justify-content-between p-1">
          <small className='font-small white-text d-flex justify-content-start'>
            <Button size="small" onClick={() => { resetAll(); setForgotPassword(!forgotPassword) }}>{forgotPassword ? 'Login' : 'Forgot Password'}</Button>
          </small>
          <small className='font-small white-text d-flex justify-content-end align-items-center'>
            <Button className="p-1" size="small" onClick={() => { resetAll(); setNewUser(!newUser) }}>{newUser ? 'Login' : 'Register'}</Button>
          </small>
        </MDBRow>
      </Dialog>
    </div>
  );
}
