import React, { useState, useEffect } from 'react';
import Button from '@material-ui/core/Button';
import { MDBCard, MDBCardBody, MDBCardImage, MDBCardTitle, MDBCardText, MDBRow, MDBCol, MDBView, MDBIcon } from 'mdbreact';
import { CSS } from "../../assets/style";
import { fetchAPI } from '../../services/Api';
import CustomBtn from '../CustomBtn';
import ViewMoreCard from './ViewMoreCard';

const BasicCard = (props: any) => {
    const [packageList, setPackageList] = useState([]);
    useEffect(() => {
        getPackageList()
    }, []);

    const getPackageList = () => {
        fetchAPI("packages").then((res: any) => {
            if (props.isLanding && res.length > 3) {
                res = res.slice(0, 3)
            }
            setPackageList(res || [])
        })
    }

    return (
        <MDBRow>
            {packageList.map((listItem: any) => <MDBCol className="mt-3" md='3' key={listItem.id}>
                <MDBCard narrow>
                    <MDBView cascade>
                        <MDBCardImage
                            cascade
                            overlay='white-slight'
                            className='card-img-top'
                            src={listItem.src}
                            alt={listItem.src}
                        />
                    </MDBView>

                    <MDBCardBody>
                        <h5 className={`${!listItem.specialInfo ? 'invisible ' : ''} pink-text`} >
                            <MDBIcon icon='fas fa-hand-holding-heart' /> {listItem.specialInfo}
                        </h5>

                        <MDBCardTitle className='font-weight-bold'>
                            {listItem.title}
                        </MDBCardTitle>

                        <MDBCardText muted>
                            {listItem.description}
                        </MDBCardText>

                        <h3 className="text-info mb-3 text-center">
                            {listItem.price}
                        </h3>

                        <div className="text-center">
                            <CustomBtn btnLabel={listItem.btnLabel} />
                        </div>
                    </MDBCardBody>
                </MDBCard>
            </MDBCol>)}
            {props.isLanding ? <MDBCol className="mt-3" md='3' key="viewMore">
                <ViewMoreCard link="services" />
            </MDBCol> : null}
        </MDBRow >
    )
}

export default BasicCard;