import React, { useEffect, useState } from 'react';
import { MDBListGroup, MDBListGroupItem } from "mdbreact";
import EmailIcon from '@material-ui/icons/Email';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import PhoneIphoneIcon from '@material-ui/icons/PhoneIphone';
import { ListItemText } from '@material-ui/core';
import { COLORS } from '../../assets/style';
import { fetchAPI } from '../../services/Api';
import get from 'lodash/get';

const ContactUsList = () => {
  const [contactDetails, setContactDetails] = useState({} as any);

  useEffect(() => {
    getContactDetails();
  }, []);

  const getContactDetails = () => {
    fetchAPI("contact").then((res: any) => {
      setContactDetails(res || {})
    })
  }
  return (
    <MDBListGroup className="mt-2">
      <MDBListGroupItem>
        <div className="d-flex w-100">
          <EmailIcon style={{ color: COLORS.app_orange }} />
          <h5 className="mb-1 ml-2" style={{ color: COLORS.app_orange }}>Feedback</h5>
        </div>
        <ListItemText className="ml-4"
          style={{ color: COLORS.app_green_light }}
          primary="We would like your feedback to improve our services:"
        // secondary="http://info@galexl.com"
        // secondaryTypographyProps={{
        //   component: "a",
        //   href: "http://info@galexl.com"

        // }}
        />

      </MDBListGroupItem>
      <MDBListGroupItem>
        <div className="d-flex w-100">
          <LocationOnIcon style={{ color: COLORS.app_orange }} />
          <h5 className="mb-1 ml-2" style={{ color: COLORS.app_orange }}>ADDRESS</h5>
        </div>
        <ListItemText style={{ color: COLORS.app_green_light }} className="ml-4"
          primary={[get(contactDetails.address, 'house', null) + " , "
            + get(contactDetails.address, 'street', null) + " , "
            + get(contactDetails.address, 'city', null) + " , "
            + get(contactDetails.address, 'state', null)]} />
      </MDBListGroupItem>
      <MDBListGroupItem>
        <div className="d-flex w-100">
          <PhoneIphoneIcon style={{ color: COLORS.app_orange }} />
          <h5 className="mb-1 ml-2" style={{ color: COLORS.app_orange }}>PHONE</h5>
        </div>
        {get(contactDetails, 'phone', []).map((phoneDetails: any, index: number) => {
          return <ListItemText style={{ color: COLORS.app_green_light }} key={index} className="ml-4" primary={phoneDetails} secondary={phoneDetails.value}
            secondaryTypographyProps={{
              component: "a",
              href: `tel:${phoneDetails.value}`
            }} />
        })}
      </MDBListGroupItem>
      <MDBListGroupItem>
        <div className="d-flex w-100">
          <EmailIcon style={{ color: COLORS.app_orange }} />
          <h5 className="mb-1 ml-2" style={{ color: COLORS.app_orange }}>EMAIL</h5>
        </div>
        {get(contactDetails, 'email', []).map((emailDetails: any, index: number) => {
          return <ListItemText style={{ color: COLORS.app_green_light }} key={index} className="ml-4" primary={emailDetails} secondary={emailDetails.value}
            secondaryTypographyProps={{
              component: "a",
              href: `mailto:${emailDetails.value}`
            }} />
        })}
      </MDBListGroupItem>
    </MDBListGroup>
  );
};

export default ContactUsList;