import React from 'react';
import moment from 'moment';
import './Calendar.css';
import { Button, Chip } from '@material-ui/core';
import { COLORS } from '../../assets/style';
import Time from './Time';
import { updateSlots } from '../../Actions/ActionTypes';
import { connect } from 'react-redux';

interface Props {
    onNextMonth?: any
    onPrevMonth?: any
    onMonthChange?: any
    onDayClick?: any
    onYearChange?: any
    slotDetails?: any
}

interface State {
    dateContext: any
    today: any
    showMonthPopup: boolean
    showYearPopup: boolean
    selectedDay: any
    showYearNav: boolean
}

export default class Calendar extends React.Component<Props, State> {
    yearInput: HTMLInputElement | null | undefined;

    constructor(props: Props) {
        super(props);
        this.state = {
            dateContext: moment(),
            today: moment(),
            showMonthPopup: false,
            showYearPopup: false,
            selectedDay: this.props.slotDetails.date.split('/')[1],
            showYearNav: true
        }
    }

    componentDidMount() {
        this.setState({
            dateContext: moment().month(parseInt(this.props.slotDetails.date.split('/')[0]) - 1),
            today: moment(),
            showMonthPopup: false,
            showYearPopup: false,
            selectedDay: this.props.slotDetails.date.split('/')[1],
            showYearNav: true
        })
    }

    weekdays = moment.weekdays(); //["Sunday", "Monday", "Tuesday", "Wednessday", "Thursday", "Friday", "Saturday"]
    weekdaysShort = moment.weekdaysShort(); // ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"]
    months: any = moment.months();

    year = () => {
        return this.state.dateContext.format("Y");
    }
    month = () => {
        return this.state.dateContext.format("MMMM");
    }
    monthIndex = () => {
        return this.state.dateContext.month();
    }
    daysInMonth = () => {
        return this.state.dateContext.daysInMonth();
    }
    currentDate = () => {
    }
    currentDay = () => {
        return this.state.dateContext.format("D");
    }

    firstDayOfMonth: any = () => {
        let dateContext = this.state.dateContext;
        let firstDay = moment(dateContext).startOf('month').format('d'); // Day of week 0...1..5...6
        return firstDay;
    }

    setMonth = (month: string) => {
        let monthNo = this.months.indexOf(month);
        let dateContext = Object.assign({}, this.state.dateContext);
        dateContext = moment(dateContext).set("month", monthNo);
        this.setState({
            dateContext: dateContext
        });
    }

    nextMonth = () => {
        let dateContext = Object.assign({}, this.state.dateContext);
        dateContext = moment(dateContext).add(1, "month");
        this.setState({
            dateContext: dateContext
        });
        this.props.onNextMonth && this.props.onNextMonth();
    }

    prevMonth = () => {
        let dateContext = Object.assign({}, this.state.dateContext);
        dateContext = moment(dateContext).subtract(1, "month");
        this.setState({
            dateContext: dateContext
        });
        this.props.onPrevMonth && this.props.onPrevMonth();
    }

    onSelectChange = (e: any, data: any) => {
        this.setMonth(data);
        this.props.onMonthChange && this.props.onMonthChange();

    }
    SelectList = (props: any) => {
        let popup = props.data.map((data: any) => {
            return (
                <div key={data}>
                    <Button variant="text" onClick={(e) => { this.onSelectChange(e, data) }}>
                        {data}
                    </Button>
                </div>
            );
        });

        return (
            <div className="month-popup">
                {popup}
            </div>
        );
    }

    onChangeMonth = (e: any, month: any) => {
        this.setState({
            showMonthPopup: !this.state.showMonthPopup
        });
    }

    MonthNav = () => {
        return (
            <>
                <Button
                    variant="text"
                    className="p-0"
                    style={{ color: COLORS.app_orange }}
                    onClick={(e) => { this.onChangeMonth(e, this.month()) }}>
                    {this.month()}
                    {this.state.showMonthPopup &&
                        <this.SelectList data={this.months} />
                    }
                </Button>
                <Chip
                    className="mr-1 ml-3"
                    onClick={() => this.onYearChangeNew(moment().year())}
                    variant="outlined"
                    size="medium"
                    label={moment().year()}
                    style={this.year() === moment().year().toString() ? { background: COLORS.app_orange, color: 'white', border: 'none' } : {}}
                />
                <Chip
                    onClick={() => this.onYearChangeNew(moment().year() + 1)}
                    variant="outlined"
                    size="medium"
                    label={moment().year() + 1}
                    style={this.year() === (moment().year() + 1).toString() ? { background: COLORS.app_orange, color: 'white', border: 'none' } : {}}
                />
            </>
        );
    }

    showYearEditor = () => {
        this.setState({
            showYearNav: true
        });
    }

    setYear = (year: any) => {
        let dateContext = Object.assign({}, this.state.dateContext);
        dateContext = moment(dateContext).set("year", year);
        this.setState({
            dateContext: dateContext
        })
    }
    onYearChangeNew = (year: any) => {
        this.setYear(year);
        this.props.onYearChange && this.props.onYearChange(year.toString());
    }

    onDayClick = (e: any, day: any) => {
        updateSlots({
            date: `${this.monthIndex() + 1}/${day}/${this.year()}`
        })
    }

    render() {
        // Map the weekdays i.e Sun, Mon, Tue etc as <td>
        let weekdays = this.weekdaysShort.map((day) => {
            return (
                <td colSpan={1} key={day} className="week-day"><b>{day}</b></td>
            )
        });

        let blanks = [];
        for (let i = 0; i < this.firstDayOfMonth(); i++) {
            blanks.push(<td colSpan={1} key={i * 80} className="emptySlot">
                {""}
            </td>
            );
        }

        const checkIfDateAlreadyPassed = (day: number) => {
            if (this.state.dateContext < new Date() && day < parseInt(this.currentDay())
            ) {
                return true
            }
            return false
        }

        const checkIfSelectedDay = (day: number) => {
            if (this.props.slotDetails.date === `${this.monthIndex() + 1}/${day}/${this.year()}`) {
                return true
            }
            return false
        }

        const disableBack = () => {
            if (this.state.dateContext < new Date()) {
                return true
            }
            return false
        }

        const disableNext = () => {
            if (parseInt(this.year()) > (moment().year() + 1)) {
                return true
            }
            return false
        }

        let daysInMonth = [];
        for (let d = 1; d <= this.daysInMonth(); d++) {
            let selectedClass = (checkIfSelectedDay(d) ? " selected-day " : "")
            let disabledClass = (checkIfDateAlreadyPassed(d) ? " disabled-day " : "")
            daysInMonth.push(
                <td key={d} >
                    <span className={`${selectedClass + disabledClass} day_all`}
                        onClick={(e) => { this.onDayClick(e, d) }}>{d}</span>
                </td>
            );
        }


        var totalSlots = [...blanks, ...daysInMonth];
        let rows: any = [];
        let cells: any = [];

        totalSlots.forEach((row, i) => {
            if ((i % 7) !== 0) {
                cells.push(row);
            } else {
                let insertRow = cells.slice();
                rows.push(insertRow);
                cells = [];
                cells.push(row);
            }
            if (i === totalSlots.length - 1) {
                let insertRow = cells.slice();
                rows.push(insertRow);
            }
        });

        let trElems = rows.map((d: any, i: any) => {
            return (
                d.length ? <tr key={i * 100}>
                    {d}
                </tr> : null
            );
        })

        return (
            <div>
                <div className="d-flex justify-content-between align-items-baseline calendar-header m-auto">
                    <i style={{ color: `${disableBack() ? 'grey' : COLORS.app_orange} ` }}
                        className="fa fa-fw fa-chevron-left"
                        onClick={(e) => {
                            !disableBack() && this.prevMonth()
                        }}>
                    </i>
                    <this.MonthNav />
                    <i style={{ color: `${disableNext() ? 'grey' : COLORS.app_orange} ` }}
                        className="fa fa-fw fa-chevron-right"
                        onClick={(e) => {
                            !disableNext() && this.nextMonth()
                        }}>
                    </i>

                </div>
                <table className="calendar w-100 m-auto">
                    <tbody className="text-center">
                        <tr>
                            {weekdays}
                        </tr>
                        {trElems}
                    </tbody>
                </table>
            </div>

        );
    }
}