import React from "react";
import { MDBContainer } from "mdbreact";
import BasicCard from "../components/Card/BasicCard";
import FeatureCard from "../components/Card/FeatureCard";
import TestimonialsPage from "../components/Card/TestimonialCard";
import Heading from "../components/Heading";
import AboutUs from "./AboutUs";
import { CSS } from "../assets/style";
import TopCarousel from "../components/TopCarousel";
import Booking from "./Booking";
import BookingStepper from '../components/Bookings/Stepper';

export default function LandingPage() {
    return (
        <>
            {/* <BookingStepper /> */}
            <TopCarousel />
            <MDBContainer>
                <Heading heading1="why" heading2="Choose Us?" />
                <FeatureCard />
            </MDBContainer>

            <div style={{ ...CSS.alignCenter }} className="text-center mt-5 parallax parallax_physio">
                <h2 className="text-light w-75 m-auto">
                    No matter whether your condition was caused by a sport, work
                    accident or otherwise, we welcome the chance to serve you.
                    <br />
                    <Booking />
                </h2>

            </div>
            <MDBContainer>
                <Heading heading1="our" heading2="Services" />
                <BasicCard isLanding={true} />
            </MDBContainer>

            {/* <MDBContainer>
                <AboutUs />
            </MDBContainer> */}

            <Heading heading1="Locate" heading2="Us" />
            <iframe
                title="map"
                width="100%"
                height="500"
                id="gmap_canvas"
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3782.3138333050524!2d73.77938061530857!3d18.559884772813724!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc2becfb828e839%3A0x5375ed2872864fd3!2s1%2C%20Pan%20Card%20Club%20Rd%2C%20Baner%2C%20Pune%2C%20Maharashtra%20411045!5e0!3m2!1sen!2sin!4v1616481108494!5m2!1sen!2sin"
                frameBorder="0"
                scrolling="no">
            </iframe>

            <MDBContainer>
                <Heading heading1="Client" heading2="Testimonials" />
                <TestimonialsPage />
            </MDBContainer>

        </>
    );
}