import { MDBCol, MDBRow } from 'mdbreact'
import React, { useEffect, useState } from 'react'
import MediaCard from '../components/Media/MediaCard'
import AddNew from '../components/Form/AddNewForm'
import InfoDialog from '../components/Dialog/InfoDialog'
import { ADD_IMAGE_FORM, ADD_VIDEO_FORM, ADD_PACKAGE_FORM } from '../services/Form'
import { fetchAPI } from '../services/Api';
import EditIcon from '@material-ui/icons/Edit';
import Slots from '../components/Form/SlotsForm'
import { get } from 'lodash'
import { Hidden, Paper } from '@material-ui/core'
import { default as GalleryImg } from '../assets/icons/picture.svg';
import { default as Video } from '../assets/icons/monitor-screen.svg';
import { default as Contact } from '../assets/icons/contact.svg';
import { default as Time } from '../assets/icons/time.svg';
import { default as Service } from '../assets/icons/package.svg';
import { default as Appointment } from '../assets/icons/agenda.svg';
import { CSS } from '../assets/style'

const SECTIONS = [{
    label: "Bookings",
    key: 'appointment',
    src: Appointment
}, {
    label: "Gallery",
    key: 'image',
    src: GalleryImg
}, {
    label: "Videos",
    key: 'video',
    src: Video
}, {
    label: "Services",
    key: 'services',
    src: Service
}, {
    label: "Timing",
    key: 'timing',
    src: Time
}, {
    label: "Contact",
    key: 'contact',
    src: Contact
}]


export default function AdminPage() {
    const [imageList, setImageList] = useState([]);
    const [videoList, setVideoList] = useState([]);
    const [operatingHours, setOperatingHours] = useState([]);
    const [contactDetails, setContactDetails] = useState({} as any);
    const [packageList, setPackageList] = useState([]);
    const [visibleSection, setVisibleSection] = useState('appointment')


    useEffect(() => {
        getImageList();
        getVideoList();
        getOperatingHours();
        getContactDetails();
        getPackageList()
    }, []);

    const getImageList = () => {
        fetchAPI("images").then((res: any) => {
            setImageList(res || [])
        })
    }

    const getVideoList = () => {
        fetchAPI("videos").then((res: any) => {
            setVideoList(res || [])
        })
    }

    const getOperatingHours = () => {
        fetchAPI("operatingSlots").then((res: any) => {
            setOperatingHours(res || [])
        })
    }

    const getContactDetails = () => {
        fetchAPI("contact").then((res: any) => {
            setContactDetails(res || {})
        })
    }

    const getPackageList = () => {
        fetchAPI("packages").then((res: any) => {
            setPackageList(res || [])
        })
    }

    const createBookingComponent = () => {
        return <h3 className="text-center">You will soon be able to see the bookings</h3>
    }

    const createImageCard = () => {
        return <>
            <InfoDialog
                btnLabel="Add Image"
                title={`Add New Image`}
                component={<AddNew cb={getImageList} {...ADD_IMAGE_FORM} />}
            />
            <MDBRow className="mt-2">
                {imageList.map((img: any) => <MDBCol key={img.id} lg="4"><MediaCard key={img.id} figure {...img} {...ADD_IMAGE_FORM} cb={getImageList} /></MDBCol>)}
            </MDBRow>
        </>
    }

    const createVideoCard = () => {
        return <>
            <InfoDialog
                btnLabel="Add Video"
                title={`Add New Video`}
                component={<AddNew cb={getVideoList} {...ADD_VIDEO_FORM} />}
            />
            <MDBRow className="mt-2">
                {videoList.map((video: any) => <MDBCol key={video.id} lg="4"><MediaCard key={video.id} {...video} {...ADD_VIDEO_FORM} cb={getVideoList} /></MDBCol>)}
            </MDBRow>
        </>
    }

    const createPackageCard = () => {
        return <>
            <InfoDialog
                btnLabel="Add Service"
                title={`Add New Service`}
                component={<AddNew cb={getPackageList} {...ADD_PACKAGE_FORM} />}
            />
            <MDBRow className="mt-2">
                {packageList.map((packages: any) => <MDBCol key={packages.id} lg="4"><MediaCard key={packages.id} figure {...packages} {...ADD_PACKAGE_FORM} cb={getPackageList} /></MDBCol>)}
            </MDBRow>
        </>
    }


    const createEditComponent = (params: any) => {
        return <Slots {...params} cb={getOperatingHours} />
    }

    const operatingHourComponent = () => {
        return <ul className="list-unstyled text-left">
            {operatingHours.map((day: any) =>
                <li key={day.key} className="p-1 border-bottom d-flex justify-content-between align-items-center">{day.label}
                    <span className={`${day.isClosed ? 'text-danger' : ''}`} > {day.isClosed ? 'Closed' : `${day.startTime} - ${day.endTime}`}
                        <InfoDialog
                            isIconButton={true}
                            iconButton={<EditIcon color="primary" />}
                            btnLabel="Edit"
                            btnVariant="contained"
                            title={`Edit ${day.label || ''}`}
                            component={createEditComponent(day)}
                        />
                    </span>
                </li>)}
        </ul>
    }

    const contactDetailsComponent = () => {
        return <ul className="list-unstyled text-left">
            <li className="p-1 border-bottom d-flex justify-content-between align-items-center">Phone
            <div className="text-right">
                    {get(contactDetails, 'phone', []).map((phone: string) => <p>{phone}</p>)}
                </div>
            </li>
            <li className="p-1 border-bottom d-flex justify-content-between align-items-center">Email
            <div className="text-right">
                    {get(contactDetails, 'email', []).map((email: string) => <p>{email}</p>)}
                </div>
            </li>
            <li className="p-1 border-bottom d-flex justify-content-between align-items-center">Address
            <div className="text-right">
                    <p>{get(contactDetails.address, 'house', null)}</p>
                    <p>{get(contactDetails.address, 'street', null)}</p>
                    <p>{get(contactDetails.address, 'city', null)}</p>
                    <p>{get(contactDetails.address, 'state', null)}</p>
                    <p>{get(contactDetails.address, 'pinCode', null)}</p>
                </div>
            </li>
            <li className="p-1 border-bottom d-flex justify-content-between align-items-center">Social Network
            {contactDetails.social ? <div className="text-right">
                    <p>Facebook : {get(contactDetails.social, 'fb', null)}</p>
                    <p>Facebook : {get(contactDetails.social, 'insta', null)}</p>
                    <p>Facebook : {get(contactDetails.social, 'twitter', null)}</p>
                </div> : null}
            </li>
        </ul>
    }

    const showSection = (section: string) => {
        if (visibleSection === section) {
            setVisibleSection('')
            return
        }
        setVisibleSection(section)
    }


    return <>
        <>
            <div className="admin_container">
                <div className="admin_menu">
                    {SECTIONS.map(section => <Paper
                        key={section.key}
                        style={{ ...CSS.alignCenter }}
                        className={`p-2 pointer ${visibleSection === section.key ? 'bg-info' : ''}`}
                        onClick={() => { showSection(section.key) }}>
                        <img className="admin_image" alt={section.key} src={section.src} crossOrigin="anonymous" />
                        <Hidden smDown>
                            <h4 className={`mt-3 ${visibleSection === section.key ? 'text-white' : ''}`}>{section.label}</h4>
                        </Hidden>
                    </Paper>)}
                </div>
                <div className="admin_content">
                    {visibleSection === 'appointment' ? createBookingComponent() : null}
                    {visibleSection === 'image' ? createImageCard() : null}
                    {visibleSection === 'video' ? createVideoCard() : null}
                    {visibleSection === 'services' ? createPackageCard() : null}
                    {visibleSection === 'timing' ? operatingHourComponent() : null}
                    {visibleSection === 'contact' ? contactDetailsComponent() : null}
                </div>
            </div>
        </>
    </>
}