import { PHONE_REGEX, REQUIRED_REGEX, EMAIL_REGEX } from "./Constants";

export const getValue = (key: string, formData: any) => {
    let formObj: any = Object.assign({}, formData);
    return formObj[key] || ""
}

export const validateFormAndMessage = (formData: any, FORM: any) => {
    let reverseFieldForValidation: Array<any> = [...FORM];
    let errorMessage = "";
    let isInvalid: boolean = false;
    reverseFieldForValidation.reverse().forEach((formElement: any) => {
        if (!formElement.pattern.test(getValue(formElement.key, formData))) {
            errorMessage = formElement.errorMessage;
            isInvalid = true
        }
    })
    return {
        isInvalid, errorMessage
    }
}

export const emptyErrorMsg = (parameter: string) => {
    return `${parameter} should not be empty.`
}

export const invalidErrorMsg = (parameter: string) => {
    return `Invalid ${parameter}`
}

