import { Chip } from '@material-ui/core';
import React, { useEffect, useState } from 'react'
import { updateSlots } from '../../Actions/ActionTypes';
import { COLORS } from '../../assets/style';

export default function Time(props: any) {
    const [selectedSlot, setSelectedSlot] = useState('');

    const slotsMenu = [];
    for (let i = 9; i <= 20; i += 1) {
        slotsMenu.push({
            label: `${i > 12 ? (i - 12) : i} :00 ${i >= 12 ? 'PM': 'AM'}`,
        })
    }

    useEffect(() => {
        setSelectedSlot(props.slotDetails.time)
    }, [props.slotDetails])

    const handleTimeSelect = (selectedSlot: string) => {
        updateSlots({
            ...props.slotDetails, ...{ time: selectedSlot }
        })
    }

    return <>{slotsMenu.map((slot: any) => <Chip
        style={slot.label === selectedSlot ? { background: COLORS.app_orange, color: 'white', border: 'none' } : {}}
        onClick={() => { handleTimeSelect(slot.label) }}
        className="m-1"
        variant="outlined"
        label={slot.label} />)}</>
}