import React, { useState } from 'react';
import { CSS } from '../../assets/style';
import { call } from '../../services/Api';
import Cookies from 'js-cookie';
import store from '../../Reducers';
import { USER_DETAILS, updateAuthUserNamePassword, updateNotofication } from '../../Actions/ActionTypes';
import { Button, TextField } from '@material-ui/core';
import { LOLGIN_FORM, getValue, validateFormAndMessage } from '../../services/Form';
import Error from '../RainyDay/Error';
import { ADMIN } from '../../services/Constants';
import CustomBtn from '../CustomBtn';

export default function Login(props: any) {
    const [formData, setFormData] = useState({ phone: '', password: '' });
    const [errorMessage, serErrorMessage] = useState('');

    const handleChange = (stateName: string, value: string) => {
        setFormData({ ...formData, [stateName]: value });
    }

    const loginUser = (e: any) => {
        e.preventDefault();
        serErrorMessage('');
        if (validateFormAndMessage(formData, LOLGIN_FORM).isInvalid) {
            serErrorMessage(validateFormAndMessage(formData, LOLGIN_FORM).errorMessage)
            return
        }
        updateAuthUserNamePassword(formData.phone, formData.password);
        Cookies.set('auth_userName', formData.phone, { expires: 30 });
        Cookies.set('auth_password', formData.password, { expires: 30 });

        call('auth/login', 'GET').then((response: any) => {
            if (response.error || response.message) {
                serErrorMessage(response.error || response.message);
                updateNotofication({ type: "error", message: response.error || response.message });
            } else {
                // cookies will expire in 30 days 
                Cookies.set('userDetails', response.userProfile || {}, { expires: 30 });
                store.dispatch({
                    type: USER_DETAILS,
                    userDetails: response.userProfile || {}
                })
                updateNotofication({ type: "success", message: `${response.userProfile.userType === ADMIN ? 'Admin' : 'User'}  Login`});
                props.successCB();
                props.modalCb(false);
            }
        })
    }

    const createTextField = (key: string, placeHolder: string, type?: string) => {
        return <TextField
            key={key}
            className="w-100"
            variant="outlined"
            size={"small"}
            value={getValue(key, formData)}
            onChange={(e) => { handleChange(key, e.target.value) }}
            margin="normal"
            label={placeHolder}
            type={type}
        />
    }

    return <form onSubmit={loginUser}>
        {LOLGIN_FORM.map(formElement => {
            return createTextField(formElement.key, formElement.label, formElement.type)
        })}
        <br />
        <CustomBtn btnLabel="Login" handleButtonClick={loginUser} />
        <Error errorMessage={errorMessage} />
    </form>
}