import { BaseUrl } from "./Constants";
// import store from "../Reducers";
import axios from "axios";
import { mock } from "../mock";
import { get } from "lodash";

const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=';
const Base64 = {
    btoa: (input: string = '') => {
        let str = input;
        let output = '';

        for (let block = 0, charCode, i = 0, map = chars;
            str.charAt(i | 0) || (map = '=', i % 1);
            output += map.charAt(63 & block >> 8 - i % 1 * 8)) {

            charCode = str.charCodeAt(i += 3 / 4);

            if (charCode > 0xFF) {
                throw new Error("'btoa' failed: The string to be encoded contains characters outside of the Latin1 range.");
            }

            block = block << 8 | charCode;
        }

        return output;
    },

    atob: (input: string = '') => {
        let str = input.replace(/=+$/, '');
        let output = '';

        if (str.length % 4 === 1) {
            throw new Error("'atob' failed: The string to be decoded is not correctly encoded.");
        }
        for (let bc = 0, bs = 0, buffer, i = 0;
            buffer = str.charAt(i++);

            ~buffer && (bs = bc % 4 ? bs * 64 + buffer : buffer,
                bc++ % 4) ? output += String.fromCharCode(255 & bs >> (-2 * bc & 6)) : 0
        ) {
            buffer = chars.indexOf(buffer);
        }

        return output;
    }
};

export function getbtoaHeader(isFormData?: boolean) {
    let userName = '';
    let password = '';

    if (isFormData) {
        return new Headers({
            'Authorization': "Basic " + Base64.btoa(userName + ':' + password),
        })
    }
    return new Headers({
        'Authorization': "Basic " + Base64.btoa(userName + ':' + password),
        'Content-Type': `${isFormData ? 'multipart/form-data' : 'application/json'}`
    })
}

export function call(url: string, method: string, body?: any) {
    return fetch(BaseUrl + url, {
        method: method,
        headers: getbtoaHeader(),
        body: body,
    }).then((response) => response.json())
        .then((responseJson) => {
            let returnResponse = new Object(responseJson)
            return returnResponse;
        })
        .catch((error) => {
            return {
                error: error.message ? 'Something went wrong...' : error
            }
        })
}

export function callFormApi(url: string, method: string, body?: any) {
    return fetch(BaseUrl + url, {
        method: method,
        headers: getbtoaHeader(true),
        body: body
    }).then((response) => response.json())
        .then((responseJson) => {
            let returnResponse = new Object(responseJson)
            return returnResponse;
        })
        .catch((error) => {
            return {
                error: error.message ? 'Something went wrong...' : error
            }
        })
}

export function fetchAPI(url: string, method?: any, data?: any) {
    let URL = `http://localhost:3000/${url}`;
    let body = undefined;
    switch (method) {
        case "delete": {
            URL = URL + '/' + data
            break
        }
        case "PUT": {
            URL = URL + '/' + data.id
            body = data
            break
        }
        case "POST": {
            body = data
        }
    }
    return axios({
        method: method || 'get',
        url: URL,
        data: body
    }).then(function (response) {
        return response.data
    })
        .catch(function (error) {
            return get(mock, url)
            // console.log(error);
        });
}