import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Calendar from '../Calendar/Calendar';
import CustomBtn from '../CustomBtn';
import Time from '../Calendar/Time';
import { connect } from 'react-redux';
import { updateSlots } from '../../Actions/ActionTypes';
import BookingDetailsForm from '../Form/BookingDetailsForm';

const useStyles = makeStyles((theme) => ({
    button: {
        marginTop: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    actionsContainer: {
        marginBottom: theme.spacing(2),
    },
    resetContainer: {
        padding: theme.spacing(3),
    },
}));

function getChip(label: string) {
    return label || ''
}

function getSteps(slotDetails: any) {
    return [`Select Date ${getChip(slotDetails?.date)}`, `Slect Time ${getChip(slotDetails?.time)}`, 'Enter Details'];
}

function getStepContent(step: number, props: any) {
    switch (step) {
        case 0:
            return <Calendar slotDetails={props.slotDetails} />
        case 1:
            return <Time slotDetails={props.slotDetails} />
        case 2:
            return <BookingDetailsForm userDetails={props.userDetails} hanldeClose={props.hanldeClose} />
        default:
            return 'Unknown step';
    }
}

function BookingStepper(props: any) {
    const classes = useStyles();
    const [activeStep, setActiveStep] = React.useState(0);
    const steps = getSteps(props.slotDetails);

    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const goToStep = (step: number) => {
        if (step > activeStep) {
            return
        }
        setActiveStep(step);
    };

    return (
        <div className="p-0 w-100">
            <Stepper activeStep={activeStep} orientation="vertical" className="p-0">
                {steps.map((label, index) => (
                    <Step key={label}>
                        <StepLabel onClick={() => { goToStep(index) }}>{label}</StepLabel>
                        <StepContent className="p-0 m-0">
                            {getStepContent(index, props)}
                            <div className={classes.actionsContainer}>
                                <div className="text-center">
                                    {activeStep ? <Button
                                        onClick={handleBack}
                                    >Back</Button> : null}
                                    {activeStep === 2 ? null : <CustomBtn
                                        disabled={activeStep === 1 && !props.slotDetails.time}
                                        handleButtonClick={handleNext}
                                        btnLabel='Next'
                                    />}
                                </div>
                            </div>
                        </StepContent>
                    </Step>
                ))}
            </Stepper>
        </div>
    );
}

const mapStateToProps = (state: any) => {
    return {
        slotDetails: state.SlotReducer.slotDetails,
        userDetails: state.UserReducer.userDetails
    }
}

export default connect(mapStateToProps, { updateSlots })(BookingStepper)
