import React, { useEffect, useState } from 'react'
import { Button, Checkbox, FormControl, FormControlLabel, InputLabel, makeStyles, MenuItem, Select } from '@material-ui/core'
import { CSS } from '../../assets/style';
import { fetchAPI } from '../../services/Api';

const useStyles = makeStyles((theme: any) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
        maxWidth: 300,
    },
    chips: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    chip: {
        margin: 2,
    },
    noLabel: {
        marginTop: theme.spacing(3),
    },
}));

export default function Slots(params: any) {
    const classes = useStyles();
    const [startTime, setStartTime] = useState('');
    const [endTime, setEndTime] = React.useState('');
    const [isClosed, setIsClosed] = useState(params.isClosed as boolean)

    useEffect(() => {
        setIsClosed(params.isClosed)
    }, [params])

    const saveSlots = () => {
        let requestParam = { ...params, isClosed, startTime, endTime }
        fetchAPI("operatingSlots", "PUT", requestParam).then(res => {
            params.cb()
        })
    }

    const slotsMenu = [];
    for (let i = 0; i <= 24; i++) {
        for (let i = 0; i <= 23; i += 1) {
            slotsMenu.push({
                label: `${i}:00`,
                value: `${i}:00`,
            })
        }
    }

    return <>
        <FormControlLabel
            value="end"
            control={<Checkbox color="secondary" />}
            label="Closed"
            labelPlacement="end"
            checked={isClosed}
            onChange={() => { setIsClosed(!isClosed) }}
        />
        <br />
        {!isClosed ?
            <div className="d-flex justify-content-around mb-3">
                <FormControl className={classes.formControl}>
                    <InputLabel id="demo-simple-select-label">Start Time</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={startTime}
                        onChange={(e: any) => setStartTime(e.target.value)}
                    >
                        {slotsMenu.map(item => <MenuItem value={item.value}>{item.label}</MenuItem>)}
                    </Select>
                </FormControl>
                <FormControl className={classes.formControl}>
                    <InputLabel id="demo-simple-select-label">End Time</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={endTime}
                        onChange={(e: any) => setEndTime(e.target.value)}
                    >
                        {slotsMenu.map(item => <MenuItem value={item.value}>{item.label}</MenuItem>)}
                    </Select>
                </FormControl>
            </div> : null}
        <div className="text-center m-3">
            <Button
                variant="contained"
                size="small"
                style={{ ...CSS.btn }}
                onClick={saveSlots}
            >Save
        </Button>
        </div>
    </>
}